import { ContextWithRoute } from './context'
import { Route } from './route'

export type Handler<In, Out> = (
	context: HandlerContext<In>,
	controller: AbortController
) => Out | Promise<Out>

export interface HandlerContext<T> extends ContextWithRoute {
	readonly data: T
}

/**
 * Execute route handlers sequentially, with cancellation support
 * @param handlers handlers to execute
 * @param context current context
 * @param controller abort controller
 * @returns {Promise<void>}
 */
export async function handle(
	handlers: Route['handlers'],
	context: ContextWithRoute,
	controller: AbortController
): Promise<void> {
	// Create a promise that rejects when aborted

	let res: unknown
	return new Promise<void>(async (resolve, reject) => {
		controller.signal.addEventListener('abort', () => reject())
		for (const handler of handlers) {
			// Check if aborted before running each handler
			if (controller.signal.aborted) return
			const ctx = { ...context, data: res } as HandlerContext<
				Parameters<typeof handler>[0]
			>
			try {
				res = await handler(ctx, controller)
			} catch (e) {
				reject(e)
			}
		}
		resolve()
	})
}
